section{
    max-width: 1000px;
    margin: 0 auto;
}

h2{
    text-align: center;
}

.img-wrap{
    text-align: center;
    margin: 20px;
}


.img-abt {
    width: 100%;
    height: auto;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out;
}

.img-abt:hover {
    transform: scale(1.5);
}

.abt-grid-wrap {
    display: flex;
    flex-direction: row;
}

.abt-paragraph-wrap {
    margin: 20px;
    margin-top: 0px;
}